import React from 'react'
import "./accest/index.css"
import logo from "../../../assets/Logo_v2.png";
import { ReactComponent as Twiter } from "../../../assets/Twitter - Negative.svg";
import { ReactComponent as Tele } from "../../../assets/Telegram - Negative.svg";
import { ReactComponent as Tum } from "../../../assets/Tumblr - Negative.svg";
import { ReactComponent as Disc } from "../../../assets/Discord - Negative.svg";
function index() {
  return (
    <footer class="footer-distributed">

      <div class="footer-left">

        <div className="logo">
          <img src={logo} alt="logo" style={{ width: 250 }} />
        </div>


      </div>

      <div class="footer-center">
        <div className='footer-center-content'>

          <p class="footer-center-1">
            <span>Contact us</span>
            <span>contac@soulsword.io</span>
          </p>

          <p class="footer-center-2">
            <span>About US</span>
            <span>WhitePaper</span>
          </p>
        </div>
      </div>

      <div class="footer-right">

        <p class="footer-company-about">
          <span>Social</span>
        </p>

        <div class="footer-icons">

          <a href="#"><Twiter /></a>
          <a href="#"><Disc /> </a>
          <a href="#"><Tele /></a>
          <a href="#"><Tum /></a>

        </div>

      </div>

    </footer >
  )
}

export default index
