import AppHeader from './header/Header';
import Sidebar from './sidebar/Sidebar';
import Footer from './footer/Footer.jsx';
import { Layout } from 'antd';
import { Outlet } from 'react-router-dom';
import { useEffect, useRef, useState } from 'react';
import { BottomNav } from './bottom-menu/BottomNav';
import { isMobile } from 'react-device-detect';
import MoneyHeader from './header/MoneyHeader';

const { Content } = Layout;

const AppLayout = () => {
    const ref = useRef();
    const [headerHeight, setHeaderHeight] = useState(0);

    useEffect(() => {
        if (ref?.current) {
            setHeaderHeight(ref.current.clientHeight);
        }
    }, [])

    return (
        <Layout  style={{ minHeight: '100vh' }}>
            <Layout className='cover' >
            <Sidebar headerHeight={headerHeight} />

            <MoneyHeader />
            </Layout >

            <Content className='cover layout-content' style={{ height: `calc(100vh - ${headerHeight}px)`, overflow: 'unset' }}>
                <Outlet />
            </Content>
            {isMobile && <BottomNav />}

            <Footer />
        </Layout>
    )
}

export default AppLayout;