import React, { useEffect, useState } from "react";
import clsx from "clsx";
import { BtnType2, Footer, ModalError } from "../../components";
import { useStyles } from "./style";
import "./style-buypage03.css";
import "../../styles/rainbowEffect.css";
import "../../styles/cardEffect.css";
import card from "../../assets/chest-04.svg";
import card2 from "../../assets/chestopen.svg";
import chest1 from "../../assets/chest-03.svg";
import chest2 from "../../assets/chest-02.svg";
import FadeIn from "react-fade-in";
import moment from "moment";
import {
  BuyCard,
  DataImage,
  GetCARD,
  DataClass,
  DataRare,
  myEffect,
} from "../../services";
import { useStoreApi } from "../../services";
import ModalConnect from "../../components/ModalConnect/ModalConnect";
import { SimpleButton, FlyEffect } from "../../components";
import ModalApprove from "../../components/ModalApprove/ModalApprove";
import LoadingPage from "../../components/LoadingPage/LoadingPage";
import Img from "react-cool-img";
import Fireword from "../../components/FireworkEfect/FireworkEffect";
import ReactAudioPlayer from "react-audio-player";
import { useWeb3React } from "@web3-react/core";
import { Progress, Tabs } from "antd";
import timebtn from "../../assets/btn2.png";
import bgitem from "../../assets/bg-item1.png";
import itempng from "../../assets/Box.png";
import { width } from "styled-system";
const FireCircle = () => {
  return (
    <div className="root">
      <div className="body">
        <div className="circle">
          <svg>
            <filter id="wavy">
              <feTurbulence
                x="0"
                y="0"
                baseFrequency="0.009"
                numOctaves="5"
                speed="2"
              >
                <animate
                  attributeName="baseFrequency"
                  dur="60s"
                  values="0.02; 0.005; 0.02"
                  repeatCount="indefinite"
                />
              </feTurbulence>
              <feDisplacementMap in="SourceGraphic" scale="30" />
            </filter>
          </svg>
        </div>
      </div>
    </div>
  );
};
const DataChest = [
  {
    id: 0,
    card1: card,
    card2: card2,
    rate1: 40,
    rate2: 30,
    rate3: 20,
    rate4: 10,
    rate5: "",
    emp: 18000,
  },
  // {
  //   id: 1,
  //   card1: chest1,
  //   card2: chest2,
  //   rate1: "",
  //   rate2: 40,
  //   rate3: 30,
  //   rate4: 15,
  //   rate5: 15,
  //   emp: 27000,
  // },
];
export const BuyPage03 = () => {
  const classes = useStyles();
  const [cardRandom, setCardRandom] = useState("");
  const [display, setDisplay] = useState(false);
  const [clickBuy, setClickBuy] = useState(false);
  // const { address, allowance, EWB } = useStoreApi();
  const { address, allowance, EWB } = useStoreApi();
  const { account } = useWeb3React();

  const [openModal, setOpenModal] = useState(false);
  const [cardOnClick, setCardOnClick] = useState("");
  const [loading, setIsLoading] = useState(false);
  const [cardPrint, setCardPrint] = useState("");
  const randomEffect = () => {
    let effectcard = myEffect[Math.floor(Math.random() * myEffect.length)];
    return effectcard.name;
  };
  const [error, setError] = useState(false);
  const [noti, setNoti] = useState("")
  const [progess, setProgess] = useState(50);
  const randomCard = async (item) => {
    let printItem;
    if (account) {
      if (allowance) {
        if (EWB < item.emp) {
          setNoti("EMP Balance is not enough")
          setError(true);
        } else {
          //
          setClickBuy(true);
          try {
            let id = await BuyCard(item.id, account).then(async (res) =>
              parseInt(res.logs[4].data.substr(-64), 16)
            );
            printItem = await turnRandomCard(id);
            setTimeout(() => {
              setCardRandom(item.card2);
              setTimeout(() => {
                setDisplay(true);
                //console.log(printItem);
                setCardPrint(printItem);
              }, 2000);
            }, 3000);
          } catch (err) {
            setNoti("Buy is failed")
            setError(true)
            setClickBuy(false);
          }
          ///
        }
      } else {

        setIsLoading(true);
      }
    } else {
      setOpenModal(true);
    }
  };

  const turnRandomCard = async (id) => {
    let classData = [];
    let rareData = [];
    let img = [];
    let effect;
    let counter = [];
    let newCard;
    try {
      await GetCARD(id).then((res) => {
        //console.log(res);
        img = DataImage.filter((item) => item.id === Number(res.idImg));
        //console.log(img)
        classData = DataClass.filter((item) => item.id === Number(res.class));

        rareData = DataRare.filter((item) => item.id === Number(res.rare));
        //counter
        counter = DataClass.filter((item) => item.name === res.counter);
        ///effect rare
        switch (rareData[0].id) {
          case 0:
            effect = "card0";
            break;
          case 1:
            effect = "card0";
            break;
          case 2:
            effect = randomEffect();
            break;
          case 3:
            effect = randomEffect();
            break;
          case 4:
            effect = "rainbow";
            break;
          default:
            break;
        }
        newCard = {
          class: classData[0].img,
          img: img[0].img,
          power: res.power,
          defence: res.defence,
          counter: counter.length !== 0 ? counter[0].img : "",
          reward: res.reward,
          level: res.level,
          exp: res.exp,
          turn: res.turn,
          rare: rareData[0].img,
          effect,
        };
      });
    } catch (err) {
      setClickBuy(false);
    }
    return newCard;
  };

  const [testCountDown, setTestCountDown] = useState({
    h: 0,
    m: 0,
    s: 0,
    d: 0,
    dateEvent: "15/09/2022",
  });
  useEffect(() => {
    let d = moment(testCountDown.dateEvent, "DD/MM/YYYY").diff(
      moment(),
      "days"
    );
    setTestCountDown((prevState) => ({
      ...prevState,
      d,
    }));
  }, []);

  //auto decrease count down by second - 1
  //d: timestamp, h: hours, m: minutes, s: seconds. when s = 0, m = m -1, when m = 0, h = h -1
  useEffect(() => {
    //calculate distance between now and dateEvent and set to d
    const interval = setInterval(() => {
      if (testCountDown.s > 0) {
        setTestCountDown((prevState) => ({
          ...prevState,
          s: prevState.s - 1,
        }));
      } else {
        if (testCountDown.m > 0) {
          setTestCountDown((prevState) => ({
            ...prevState,
            m: prevState.m - 1,
            s: 59,
          }));
        } else {
          if (testCountDown.h > 0) {
            setTestCountDown((prevState) => ({
              ...prevState,
              h: prevState.h - 1,
              m: 59,
              s: 59,
            }));
          } else {
            if (testCountDown.d > 0) {
              setTestCountDown((prevState) => ({
                ...prevState,
                d: prevState.d - 1,
                h: 23,
                m: 59,
                s: 59,
              }));
            }
          }
        }
      }
    }, 1000);
    return () => clearInterval(interval);
  }, [testCountDown]);

  const items = [
    {
      label: 'Countdown', key: 'item-1', children:
        <div className="countdown-tab">
          <div>
            <div className="main-content">
              <div className="d-flex" style={{ display: "flex", width: "100%", columnGap: "80px" }}>
                <div className="panel2" style={{ width: "50%", alignSelf: "center" }}>

                  <div className={clsx("frame", classes.frame)}>
                    <div className={classes.containCard1} >
                      <Img
                        src={itempng}
                        className={clsx("chest", classes.imgCardDefault)}
                      />
                      <div className="item-name" style={{ textAlign: "left", fontSize: "26px", fontWeight: "800" }}><span style={{ color: "#18F2EF" }}>SOUL</span> <span style={{ color: "#fff" }}>TREASURE</span></div>
                      <div className="item-content" style={{ textAlign: "left", fontSize: "14px", fontWeight: "500" }}>
                        Buying a soul box allows you to own a soul to be able to unlock the sword's hidden attribute to help the sword increase its power to be able to conquer the boss at the highest level
                      </div>
                    </div>
                  </div>
                </div>
                <div className="panel1" style={{ width: "50%", alignSelf: "center" }}>
                  <div>
                    <div style={{
                      fontWeight: "700",
                      fontSize: "24px",
                    }}>This Round of The Sale is Finished</div>
                    <div className="timeBtn">
                      <div>
                        <div>10</div>
                        <div>Days</div>
                      </div>
                      <div>
                        <div>00</div>
                        <div>Hours</div>
                      </div>
                      <div>
                        <div>10</div>
                        <div>Minutes</div>
                      </div>
                      <div>
                        <div>00</div>
                        <div>seconds</div>
                      </div>
                    </div>
                    <div className="btnpay-bg">
                      <div className="btn-pay">PAY</div>
                    </div>
                  </div>
                </div>

              </div>
            </div>
          </div>

        </div>
    }, // remember to pass the key prop
    {
      label: 'Sale In Live', key: 'item-2', children:
        <div className="salelive-tab" style={{ marginTop: "70px" }}>
          <div>
            <div className="main-content">
              <div className="d-flex" style={{ display: "flex", width: "100%", columnGap: "80px" }}>
                <div className="panel2">
                  <div className={clsx("frame", classes.frame)}>
                    <div className={classes.containCard1} >
                      <Img
                        src={itempng}
                        className={clsx("chest", classes.imgCardDefault)}
                      />
                      <div className="item-name"><span style={{ color: "#18F2EF" }}>SOUL</span> <span style={{ color: "#fff" }}>TREASURE</span></div>
                      <div className="item-content" style={{ textAlign: "left", fontSize: "14px", fontWeight: "500" }}>
                        Buying a soul box allows you to own a soul to be able to unlock the sword's hidden attribute to help the sword increase its power to be able to conquer the boss at the highest level
                      </div>
                    </div>
                  </div>
                </div>
                <div className="panel1" style={{ height: "100%", width: "75%" }}>
                  <div className={clsx("frame", classes.board)}>
                    <div style={{ marginTop: "-100px", position: "relative" }}>
                      <div className="header-time">
                        This Round of The Sale is Finised
                      </div>
                      <div className="timeBtn">

                        <div>
                          <div>10</div>
                          <div>Days</div>
                        </div>
                        <div>
                          <div>00</div>
                          <div>Hours</div>
                        </div>
                        <div>
                          <div>10</div>
                          <div>Minutes</div>
                        </div>
                        <div>
                          <div>00</div>
                          <div>seconds</div>
                        </div>
                      </div>
                    </div>
                    <div className="sale-content">
                      <div className="item-name" style={{ fontSize: "40px" }}><span style={{ color: "#18F2EF" }}>SOUL</span> <span style={{ color: "#000" }}>TREASURE</span></div>
                      <div className="item-content" style={{ textAlign: "left", fontSize: "18px", fontWeight: "500", color: "rgba(83, 82, 82, 0.6)" }}>
                        Buying a soul box allows you to own a soul to be able to unlock the sword's hidden attribute to help the sword increase its power to be able to conquer the boss at the highest level
                      </div>
                      <div style={{ display: "flex", justifyContent: "space-between", marginTop: "20px" }}>
                        <div>
                          <div style={{ color: "#fff", fontSize: "24px" }}>Price</div>
                          <div style={{ color: "#868686", fontSize: "24px" }} className="currPrice">0.0014 BNB</div>
                          <div style={{ color: "#006472", fontSize: "24px", fontWeight: "800" }} className="newPrice">5000 SWG</div>
                        </div>
                        <div style={{ alignSelf: "center" }}>
                          <div className="btnpay-bg" >
                            <div className="btn-pay" >Buy</div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={`${classes.progessBar} progessBar`} style={{ flexDirection: "column" }}>
                      <div style={{ color: "#fff" }}><span style={{ fontSize: "16px", fontWeight: "500", }}>Sell limit:</span> <span style={{ fontSize: "23px", fontWeight: "700" }}>1000/2000</span></div>
                      <div className="progess-bar-content" >
                        <Progress percent={progess} size="default" showInfo={false} />
                      </div>
                    </div>
                  </div>

                </div>

              </div>
            </div>
          </div>

        </div>
    },
    {
      label: 'Sold Out', key: 'item-3', children:
        <div className="salelive-tab soldout-tab" style={{ marginTop: "70px" }}>
          <div>
            <div className="main-content">
              <div className="d-flex" style={{ display: "flex", width: "100%", columnGap: "80px" }}>
                <div className="panel2">
                  <div className={clsx("frame", classes.frame)}>
                    <div className={classes.containCard1} >
                      <Img
                        src={itempng}
                        className={clsx("chest", classes.imgCardDefault)}
                      />
                      <div className="item-name"><span style={{ color: "#18F2EF" }}>SOUL</span> <span style={{ color: "#fff" }}>TREASURE</span></div>
                      <div className="item-content" style={{ textAlign: "left", fontSize: "14px", fontWeight: "500" }}>
                        Buying a soul box allows you to own a soul to be able to unlock the sword's hidden attribute to help the sword increase its power to be able to conquer the boss at the highest level
                      </div>
                    </div>
                  </div>
                </div>
                <div className="panel1" style={{ height: "100%", width: "75%" }}>
                  <div className={clsx("frame", classes.board)}>
                    <div style={{ marginTop: "-100px", position: "relative" }}>
                      <div className="header-time">
                        This Round of The Sale is Finised
                      </div>
                      <div className="timeBtn">

                        <div>
                          <div>10</div>
                          <div>Days</div>
                        </div>
                        <div>
                          <div>00</div>
                          <div>Hours</div>
                        </div>
                        <div>
                          <div>10</div>
                          <div>Minutes</div>
                        </div>
                        <div>
                          <div>00</div>
                          <div>seconds</div>
                        </div>
                      </div>
                    </div>
                    <div className="sale-content">
                      <div className="item-name" style={{ fontSize: "40px" }}><span style={{ color: "#18F2EF" }}>SOUL</span> <span style={{ color: "#000" }}>TREASURE</span></div>
                      <div className="item-content" style={{ textAlign: "left", fontSize: "18px", fontWeight: "500", color: "rgba(83, 82, 82, 0.6)" }}>
                        Buying a soul box allows you to own a soul to be able to unlock the sword's hidden attribute to help the sword increase its power to be able to conquer the boss at the highest level
                      </div>
                      <div style={{ display: "flex", justifyContent: "space-between", marginTop: "20px" }}>
                        <div>
                          <div style={{ color: "#fff", fontSize: "24px" }}>Price</div>
                          <div style={{ color: "#868686", fontSize: "24px" }} className="currPrice">0.0014 BNB</div>
                          <div style={{ color: "#006472", fontSize: "24px", fontWeight: "800" }} className="newPrice">5000 SWG</div>
                        </div>
                        <div style={{ alignSelf: "center" }}>
                          <div className="btnpay-bg" >
                            <div className="btn-pay" >Buy</div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={`${classes.progessBar} progessBar`} style={{ flexDirection: "column" }}>
                      <div style={{ color: "#fff" }}><span style={{ fontSize: "16px", fontWeight: "500", }}>Sell limit:</span> <span style={{ fontSize: "23px", fontWeight: "700" }}>1000/2000</span></div>
                      <div className="progess-bar-content" >
                        <Progress percent={progess} size="default" showInfo={false} />
                      </div>
                    </div>
                  </div>

                </div>

              </div>
            </div>
          </div>
         
        </div>
    },
  ];
  return (
    <div className="font-face-gm">
      <FlyEffect />
      <div className="header-buyPage">
        <Tabs type="card" items={items} />
      </div>

      {/* <FlyEffect />
      <div className="body-buyPage1">
        <FadeIn delay={5}>
          {!clickBuy ? null : <FireCircle />}
          <div>
            {!clickBuy ? (
              <div className={clsx("containCard2", classes.containCard2)}>
                {DataChest.map((item, index) => (
                  <div className={clsx("frame", classes.frame)}>
                    <div className={classes.containCard1} key={index}>
                      <Img
                        src={item.card1}
                        className={clsx("chest", classes.imgCardDefault)}
                      />
                      <div className="chestContent">
                        <h3 className="chestContent-title" style={{ color: "#fff" }}>
                          {item.emp
                            .toString()
                            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}{" "}
                          $SS
                        </h3>
                      
                      </div>
                      <SimpleButton
                        title={allowance ? "COLLECT" : "APPROVE"}
                        color={index === DataChest.length - 1 ? "red" : "#000"}
                        backGround={
                          index === DataChest.length - 1 ? "#000" : "red"
                        }
                        onClick={() => {
                          randomCard(item);
                          setCardOnClick(item.card1);
                        }}
                        className="btnBuy"
                      />
                    </div>
                  </div>
                ))}
              </div>
            ) : cardRandom == "" ? (
              <div>
                <Img src={cardOnClick} className="image-bounce" />
              </div>
            ) : !display ? (
              <FadeIn>
                <Img
                  src={cardRandom}
                  className={clsx("chest", classes.imgCardDefault2)}
                />
              </FadeIn>
            ) : (
              cardPrint !== "" && (
                <FadeIn>
                  <div className={classes.fireword}>
                    <Fireword />
                  </div>
                  <ReactAudioPlayer
                    src="http://nhacchuongvui.com/wp-content/uploads/Tieng-Phao-No-www_nhacchuongvui_com.mp3?_=1"
                    autoPlay
                  />
                  <div className={classes.relative}>
                    <Img
                      src={cardPrint.img}
                      className={clsx(classes.imgCardBuyPage)}
                    />
                    <Img
                      src={cardPrint.rare}
                      className={clsx("numberCard3-buyPage", classes.cardPrint)}
                    />
                    <Img
                      className="gemPurple-buyPage"
                      src={cardPrint.class}
                      className={classes.class}
                    />
                    <div className="img-content-buyPage">
                      <div className="img-content-buyPage-left">
                        <p>POWER : {cardPrint.power}</p>
                        <p>DEFENCE : {cardPrint.defence}</p>
                        <div className={classes.layout1}>
                          <div>COUNTER : </div>
                          {cardPrint.counter === "" ? (
                            ""
                          ) : (
                            <img
                              className={clsx(
                                "gemPurple-couter-buyPage",
                                classes.counter
                              )}
                              src={cardPrint.counter}
                            />
                          )}
                        </div>
                        <p>BONUS REWARD : {cardPrint.reward}%</p>
                      </div>
                      <div className="img-content-buyPage-right">
                        <p>LEVEL : {cardPrint.level}</p>
                        <p>EXP : {cardPrint.exp}</p>
                        <p>TURN : {cardPrint.turn}</p>
                      </div>
                    </div>
                  </div>

                  <div
                    className={classes.containBtn}
                    onClick={() => {
                      setCardRandom("");
                      setCardPrint("");
                      setClickBuy(false);
                    }}
                  >
                    <SimpleButton title="OK" color="red" backGround="#000" />
                  </div>
                </FadeIn>
              )
            )}
          </div>

          <div style={{ color: "#fff", textAlign: "center", fontSize: "20px", marginTop: "20px" }}>
            <div>
              Price: 0.5 BNB
            </div>
            <div >
              For sale now: 300
            </div>
          </div>
          <div style={{ display: "flex", justifyContent: "space-between", color: "#fff", fontSize: "20px" }}>
            <div>SOLD: 100</div>
            <div>REMIND: 200</div>
          </div>
        </FadeIn>

      </div>


      <div>
        <Footer />
      </div>
      {error && (
        <ModalError errors={noti} onClick={setError} />
      )}
      {loading ? <LoadingPage /> : null}
      {!allowance ? <ModalApprove /> : null}
      {openModal && <ModalConnect setOpen={setOpenModal} />} */}
    </div>
  );
};
